// utility classes
.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  @include mq(sm) {
    max-width: 34em;
  }
  @include mq(md) {
    max-width: em(750);
  }
  @include mq(lg) {
    max-width: em(970);
  }
  @include mq(xl) {
    max-width: em(1200);
  }
}
.inset {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.flex {
  display: flex;
}
.block {
  display: block;
}
.inline-block {
  display: inline-block;
}
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}