// set base typography styles

// import font from google fonts
@import url('https://fonts.googleapis.com/css?family=Nunito+Sans:400,600,800');

//set base fontsize
html {
  font-size: 100%; //16px

}
body {
  font-size: 1em; //16px
  font-family: $ff;
}

// set font family and font weight for headings
h1, h2, h3, h4, h5 {
  font-weight: $fw-semibold;
}

